/******************************************************************
  - MIXINS -
******************************************************************/
.acf-block-preview .contact-form {
  pointer-events: none;
}

.contact-form {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.contact-form .contact_form-title p {
  margin-bottom: 50px;
}
.contact-form .contact_form .wpforms-field-container {
  padding: 30px;
}
.contact-form .contact_form .wpforms-field-container input, .contact-form .contact_form .wpforms-field-container textarea {
  margin-bottom: 20px;
  height: 60px;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  border-radius: 3px;
  font-size: 1.25rem;
  width: 100%;
}
.contact-form .contact_form .wpforms-field-container textarea {
  height: 180px;
  resize: none;
}
.contact-form .contact_form .wpforms-field-description {
  margin-top: 20px;
}
.contact-form .contact_form .input_field:not(:-moz-placeholder-shown) ~ .input_label {
  z-index: -1;
}
.contact-form .contact_form .input_field:not(:-ms-input-placeholder) ~ .input_label {
  z-index: -1;
}
.contact-form .contact_form .input_field:focus ~ .input_label,
.contact-form .contact_form .input_field:not(:placeholder-shown) ~ .input_label {
  z-index: -1;
}
.contact-form .contact_form .wpforms-submit-container {
  text-align: center;
}
.contact-form .contact_form .wpforms-submit-container button.wpforms-submit {
  margin-top: 20px;
}
.contact-form .wpforms-container input.wpforms-field-medium, .contact-form .wpforms-container select.wpforms-field-medium, .contact-form .wpforms-container .wpforms-field-row.wpforms-field-medium {
  max-width: none;
}

@media print, screen and (min-width: 40em) {
  .contact-form .contact_form-inner {
    padding: 50px;
  }
  .contact-form .contact_form input, .contact-form .contact_form textarea {
    margin-bottom: 40px;
  }
}
@media print, screen and (min-width: 64em) {
  .contact-form {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}